import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";

@Component({
    selector: "app-header",
    templateUrl: "./header.component.html",
    styleUrls: ["./header.component.scss"]
})
export class HeaderComponent implements OnInit {
    constructor(private route: ActivatedRoute, private router: Router) {}

    ngOnInit() {
        let hamburger = document.querySelector(".hamburger");
        let hide = document.querySelector(".hide");
        let hide2All = document.querySelectorAll(".hide2 a");

        hamburger.addEventListener("click", function() {
            // Toggle class "is-active"
            hamburger.classList.toggle("is-active");
            hide.classList.toggle("is-flex");

            hide.addEventListener("click", function() {
                hamburger.classList.remove("is-active");
                hide.classList.remove("is-flex");
            });
        });
        if (window.matchMedia("(max-width: 768px)").matches) {
            for (let i = 0; i < hide2All.length; i++) {
                hide.appendChild(hide2All[i]);
            }
        }
    }
    go(str) {
        let sort = this.route.snapshot.params["sort"];
        this.router.navigate(["/" + str + "/" + sort]);
    }
}



